<template>
  <audio
    :src="url"
    id="player"
    preload="true"
    @canplay="startPlay"
    @timeupdate="timeupdate"
    @ended="ended"
  ></audio>
</template>

<script>
import axios from "axios";
import { mapMutations, mapGetters } from "vuex";

export default {
  data() {
    return {
      host: this.$store.state.HOST
    };
  },
  computed: {
    ...mapGetters([
      "id",
      "url",
      "songsList",
      "isPlay",
      "curTime",
      "changeTime",
      "autoNext"
    ])
  },
  watch: {
    isPlay: function() {
      this.togglePlay();
    },
    id: function() {
      this.getSong();
    },
    changeTime: function() {
      //  
      player.currentTime = this.changeTime;
    },
    //  IOS
    url: function() {
      var _this = this;
      axios
        .get(this.host + "/lyric", {
          params: {
            id: 347230
          }
        })
        .then(function() {
          var player = document.querySelector("#player");
          player.play();
        });
    }
  },
  methods: {
    //  \
    togglePlay() {
      var player = document.querySelector("#player");
      if (this.isPlay) {
        player.play();
      } else {
        player.pause();
      }
    },
    //  
    getSong() {
      var _this = this;
      var player = document.querySelector("#player");
      this.$store.commit("setCurTime", 0);
      this.$store.commit("setIsPlay", false);
      axios
        .get(_this.host + "/music/url", {
          params: {
            id: _this.id
          }
        })
        .then(function(res) {
           
           
          if (!res.data.data[0].url) {
            _this.$store.commit("setUrl", "");
            return false;
          }
          _this.$store.commit("setUrl", res.data.data[0].url);
        });
    },
    //  
    startPlay() {
      //  
      this.$store.commit("setDuration", player.duration);
      //  
      player.play();
      this.$store.commit("setIsPlay", true);
    },
    //  
    ended() {
      this.$store.commit("setIsPlay", false);
      this.$store.commit("setCurTime", 0);
      this.$store.commit("setAutoNext", !this.autoNext);
    },
    timeupdate() {
      this.$store.commit("setCurTime", player.currentTime);
    }
  }
};
</script>

<style>
#player {
  display: none;
}
</style>

